import '../styles/ProjectItem.scss';
import checkItem from '../tools/checkItem';

const Browser = () => (
    <>
    </>
);

const Phone = (props) => (
    <video className="project-preview" autoPlay loop muted>
        <source src={props.main} type="video/webm" />
        <source src={props.fallback} type="video/mp4" />
    </video>
);

const ProjectItem = (props) => {
    const item = props.item;
    const lang = props.languageNum;
    console.log(props.item);
    const projectType = (item.type === 'Phone')
                    ? <Phone main={item.sourceMain} fallback={item.sourceFallback} />
                    : <Browser />;
    const projectAvailability = (item.availability !== undefined)
                    ? <h3>Available at:</h3>
                    : <></>;
    const order = (item.index % 2 == 0)
                    ? 'order-last'
                    : 'order-first';


    return (
        <div className="project-item">
            <div className="row">
                <div className="col-7 project-pad">
                    <div className="project-text">
                        <h1>{item.title}</h1>
                        <p>{checkItem(item.description, lang)}</p>
                        {projectAvailability}
                        <div>
                            { item.availability !== undefined &&
                                item.availability.map((site, index) => (
                                    <a 
                                        key={index} 
                                        href={site.link} 
                                        className="project-link">{site.title}</a>
                                ))
                            }
                        </div>
                    </div>
                </div>
                <div className={`col-5 project-pad ${order}`}>
                    {projectType}
                </div>
            </div>
        </div>
    )
}

export default ProjectItem;